.group-header{
  margin: 20px 0px 5px 0px;
  font-size: 15px;
}

.App-header{
  font-size: 30px;
}
.btn-primary{
  background-color: transparent;
  border: 1px solid #b4b2b2;
  color: #b4b2b2;
  font-size: 15px;
  padding: 5px 10px;
  margin-top: 20px;
  cursor: pointer;
}

.btn-primary:hover{
  background-color: #b4b2b2;
  color: #192a4b;
}

input{
  border: 0px;
  border-bottom: 1px solid #b4b2b2;
  padding: 5px 10px;
  font-size: 15px;
  background: transparent;
  color: #b4b2b2;
}

input:focus{
  outline: none;
  border-bottom: 1px solid #fff;
  color: #fff;
}

body {
	background: #272b33;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.build-status .message{
  margin: 0px 0px 20px 0px;
  font-size: 20px;
}


#submit-btn{
  margin-left: 50px;
}

.divider-line{
  margin: 20px 0px;
  border-bottom: 1px solid #b4b2b2;
}

.ci-form{
  width: 100%;
}

.licence-list{
  width: 100%;
  display: flex;
}

.licence-group{
  margin-right: 20px;
}